export const GET_GEOLOCATION = 'GET_GEOLOCATION'
export const SET_GEOLOCATION = 'SET_GEOLOCATION'

// Get the user's lat/long coordinates using the HTML5 Geolocation API
export const getGeoLocation = () => {
  console.log('getGeoLocation fired')

  return dispatch => {
    dispatch({ type: 'GET_GEOLOCATION' })

    // Check if browser allows for geolocation
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          getGeoSuccess(position, dispatch)
        },
        (error) => {
          getGeoError(error, dispatch)
        }
      )
    } else {
      // Browser does not support Geolocation api
      console.error('Browser does not support Geolocation API!!')
      alert('The client device does not support HTML5 Geolocation API')
      // TODO: Dispatch error state
    }
  }
}

// The initial geolocation state object
const initialState = {
  coords: {
    lat: 0,
    long: 0
  },
  loaded: false,
  status: 'ok'
}


// Handle HTML5 Geolocation API Success
const getGeoSuccess = (position, dispatch) => {
  dispatch({
    type: 'SET_GEOLOCATION',
    coords: {
      lat: position.coords.latitude,
      long: position.coords.longitude
    },
    loaded: true,
    status: 'ok'
  })
}

// Handle HTML5 Geolocation API Error
const getGeoError = (error, dispatch) => {
  console.error('Getting Geolocation Failed!')
  console.error('getGeoError => ERROR:', error)
  // Process Geolocation API error codes
  switch (error.code) {
    case error.PERMISSION_DENIED:
      alert('User denied the request for Geolocation.')
      break
    case error.POSITION_UNAVAILABLE:
      alert('Location information is unavailable.')
      break
    case error.TIMEOUT:
      alert('The request to get user location timed out.')
      break
    case error.UNKNOWN_ERROR:
      alert('An unknown error occurred.')
      break
  }
}

