import { GET_GEOLOCATION } from '../actions/actionCreators'
import { SET_GEOLOCATION } from '../actions/actionCreators'

// The Geolocation Reducer
const geolocationReducer = (state = [], action) => {

    switch (action.type) {
        // Get location action is an async getter, so state doesn't change'
        case GET_GEOLOCATION:

            return state

        // Set the new geolocation state
        case SET_GEOLOCATION:

            return Object.assign({}, state, {
                coords: action.coords,
                status: action.status,
                loaded: action.loaded
            })

        // Default State
        default:
            return state
    }
}

export default geolocationReducer


