import React from 'react'
import Typist from 'react-typist'
import { connect } from 'react-redux'

import * as actionCreators from '../../actions/actionCreators'

import './Terminal.scss'

// Typist Cursor
const cursor = {
  show: true,
  blink: true,
  element: '',
  hideWhenDone: false,
}

// Random Hash Generator
const randomHash = () => {
  return  Math.random().toString(36).substr(2, 30)
}

// Done Typing
const setIntroTyped = (dispatch) => {
    console.log('Done Typing 1')

    dispatch( actionCreators.getGeoLocation() )
}

// Terminal
export const Terminal = (props) => (

    <div className='terminal'>
    { console.log('terminal props:', props) }

        <div className='terminal-text-container'>
            <div className='terminal-text'>
                <span className='line line-bold'> <span className='highlight'>[ PlanetScope AI ]</span>  <br/><br/></span>
                <span className='line line-header'> <span className='white'> <span className='line-bold'>Welcome to PlanetScope AI:</span> <br/>---------------------------<br/> Advanced GeoSpatial Deep Learning Platform </span></span>

                <Typist cursor={cursor} avgTypingDelay={5} avgTypingSpeed={5}  onTypingDone={ () => setIntroTyped(props.dispatch) } >
                    <br/>

                    <span className='line'> <span className='green'>&gt;&gt;&gt;</span> Initializing startup sequence. <span className='white'>Platform GEN v0.1.5 BETA</span> <br/></span>
                    <span className='line'> <span className='caret'>&gt;</span> Generating new build profile.................... success! <span className='grey'>ID: { randomHash() }_2016</span><br /></span>
                    <span className='line'> <span className='caret'>&gt;</span> Mounting build into system memory <span className='green'>&#10003;</span><br /></span>
                    <span className='line'> <span className='caret'>&gt;</span> Initializing redux capacitator subsys <span className='green'>&#10003;</span><br/></span>
                    <span className='line'> <span className='caret'>&gt;</span> Reducing virtual stores Inode-cache tables <span className='green'>&#10003;</span><br/></span>
                    <span className='line'> <span className='caret'>&gt;</span> NET: Registering protocol family 16 A-Frame <span className='green'>&#10003;</span><br/></span>
                    <span className='line'> <span className='caret'>&gt;</span> Io scheduler cfq registered <span className='green'>&#10003;</span><br/></span>
                    <span className='line'> <span className='caret'>&gt;</span> Frame buffer device 82x26 module loaded. Systems Nominal. <span className='green'>&#10003;</span><br/></span>
                    <span className='line yellow'> <span className='caret yellow'>&#9888;</span> High-Speed Isochronous Endpoints Enabled....... Loading GPS pre-processors. <br/></span>
                    <span className='line'> <span className='caret'>&gt;</span> COMS_1: Attempting coordinate lock ...<br/></span>

                    <span className='line'> <span className='caret'>&gt;</span> <span className='white '><strong>USER INPUT REQUESTED:</strong> Please Enable Location API!</span> <br/></span>
                </Typist>

                {( props.geolocation.loaded
                    ? <span><span className='caret'>&gt;</span> Latitude: {props.geolocation.coords.lat}, Longitutde: {props.geolocation.coords.long} </span>
                    : false
                )}

            </div>
        </div>

    </div>
)


Terminal.propTypes = {
  geolocation: React.PropTypes.object.isRequired
}

/*const mapDispatchToProps = (dispatch) => {
   return bindActionCreators(actionCreators, dispatch)
}*/

const mapStateToProps = (state) => ({
  geolocation : state.geolocation
})


export default connect(mapStateToProps/*, mapDispatchToProps*/)(Terminal)

//export default Terminal


