import { createStore, applyMiddleware, compose } from 'redux'
import { syncHistoryWithStore } from 'react-router-redux'
import { browserHistory, hashHistory } from 'react-router'
import createLogger from 'redux-logger';
import rootReducer from '../reducers/index'
import thunk from 'redux-thunk';


const initialState = {
  geolocation: {
    coords: {
      lat: 0,
      long: 0
    },
    status: 'ok',
    loaded: false
  }
}

const logger = createLogger({
  collapsed: true,
  predicate: () =>
    process.env.NODE_ENV === `development`, // eslint-disable-line no-unused-vars
});

const enhancers = compose(
  window.devToolsExtension ? window.devToolsExtension() : f => f
)

const createStoreWithMiddleware = applyMiddleware(
  thunk,
  logger
)(createStore);

const store = createStoreWithMiddleware(rootReducer, initialState, enhancers)


// we export history because we need it in `reduxstagram.js` to feed into <Router>
export const history = syncHistoryWithStore(hashHistory, store)

/*
  Enable Hot Reloading for the reducers
  We re-require() the reducers whenever any new code has been written.
  Webpack will handle the rest
*/
if(module.hot) {
  module.hot.accept('../reducers/index', () => {
    const nextRootReducer = require('../reducers/index').default
    store.replaceReducer(nextRootReducer)
  })
}

export default store



